export default class W3Util {
    constructor(web3) {
        this.web3 = web3;
    }
    estimateGas(opts) {
        return new Promise((resolve, reject) => {
            this.web3.eth.estimateGas(opts, (e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    networkGasPrice() {
        return new Promise((resolve, reject) => {
            this.web3.eth.getGasPrice((e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    getReceipt(txHash) {
        return new Promise((resolve, reject) => {
            this.web3.eth.getTransactionReceipt(txHash, (e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    getBlockNumber() {
        return new Promise((resolve, reject) => {
            this.web3.eth.getBlockNumber((e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    getBlock(number = 'latest') {
        return new Promise((resolve, reject) => {
            this.web3.eth.getBlock(number, (err, block) => {
                if (!err)
                    if (block)
                        resolve(block);
                    else
                        reject(`Returned block ${number} is null`);
                else
                    reject(err);
            });
        });
    }
    isWatchingEnabled() {
        return new Promise((resolve) => {
            this.web3.currentProvider.sendAsync({
                jsonrpc: '2.0',
                id: 1,
                method: 'eth_getFilterLogs',
                params: []
            }, (err) => {
                if (err !== null) {
                    resolve(false);
                }
                resolve(true);
            });
        });
    }
}
